import { Component, Input, OnChanges, OnDestroy, OnInit } from '@angular/core';
import { AuthService } from 'src/app/core/services';
import { Filters } from '../../models/filter.model';
import { MobilePhoneUngrouped } from '../../models/mobile-phone.model';
import { Sort } from '../../models/sort.model';
import { FilterProductsService } from '../../services/filter-products.service';
import { MobileService } from '../../services/mobile.service';


@Component({
  selector: 'app-product-view',
  templateUrl: './product-view.component.html',
  styleUrls: ['./product-view.component.scss'],
  providers: [MobileService]

})
export class ProductViewComponent implements OnInit {
  @Input('mobiles') mobiles: MobilePhoneUngrouped[] = []
  @Input() type
  constructor(
    private authSrvc: AuthService
  ) { }
  ngOnInit() {
    this.getRole();
    this.getIsVerified()
    this.getBusinessStatus()
  }

  role: string = ""
  getRole() {
    this.role = this.authSrvc.role
  }
  IsVerified: boolean
  getIsVerified() {
    this.IsVerified = this.authSrvc.isVerified
  }
  businessStatus:string="";
  getBusinessStatus(){
    this.businessStatus=this.authSrvc.businessStatus
  }
}
