export class OrderPaypal {
  public name: string;
  public quantity: string;
  public category: string;
  public description: string;
  public sku: string;
  public unit_amount: UnitAmount
  constructor(name, quantity, description, sku, unit_amount) {
    this.name = name;
    this.quantity = quantity;
    // this.category = "PHYSICAL_GOODS" || "DIGITAL_GOODS";
    this.category = "PHYSICAL_GOODS"
    this.description = description;
    this.sku = sku//details
    this.unit_amount = unit_amount
  }


}

export class UnitAmount {
  public currency_code: string;
  public value: string

  constructor(value, currency_code = 'USD') {
    this.value = value;
    this.currency_code = currency_code
  }
}


export class PurchaseUnits {
  public description: string;
  public amount: Amount
  constructor(description, amount) {
    this.description = description;
    this.amount = amount;
  }
}

export class Amount {
  public currency_code: string;
  public value: string;

  constructor(value, currency_code = 'USD') {
    this.value = value;
    this.currency_code = currency_code
  }
}