import { ElementRef, EventEmitter, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertService } from 'src/app/core/services';
import { threadId } from 'worker_threads';
import { ProductCartService } from '../../services/ProductCart.service';
import { OrderItem, Address, PAYMENTMETHOD, Order } from "../checkout/order-over-view.model"
import { DeliverAddressComponent } from '../deliver-address/deliver-address.component';
import { OrderPaypal, UnitAmount } from '../deliver-address/order.paypal';

@Component({
  selector: 'app-order-overview',
  templateUrl: './order-overview.component.html',
  styleUrls: ['./order-overview.component.scss'],
  // providers: [ProductCartService]
})
export class OrderOverviewComponent implements OnInit {
  // @ViewChild(DeliverAddressComponent) addressInfo: DeliverAddressComponent;
  @Output() changeTap = new EventEmitter();
  checkNotEmptyField: boolean = false;
  checkIfProductExist: boolean = false;
  useAddressInformation: boolean
  useDeliveryAddressAsInvoiceAddress: boolean
  userId: string

  invoiceAddress: Address;
  deliveryAddress: Address;
  orderItem: OrderItem[];
  paymentMethod: any;
  constructor(private ProductCartService: ProductCartService, private router: Router, private alertService: AlertService) { }


  ngOnInit(): void {
    this.getAllData()
  }

  ngAfterViewInit() {
    this.getTotal(this.orderItem);
  }

  productsTotalPrice = 0;
  getTotal(products: OrderItem[]) {
    this.productsTotalPrice = 0;
    if (products?.length > 0) {
      for (let prod of products)
        this.productsTotalPrice += prod['productPrice'] * prod.quantity;
      return this.productsTotalPrice;
    }
  }

  getAllData() {
    this.ProductCartService.orderOverViewData$.subscribe(data => {
      this.deliveryAddress = data?.['OrderAddress']?.['deliveryAddress'];
      this.invoiceAddress = data?.['OrderAddress']?.['invoiceAddress'];
      this.useAddressInformation = data?.['OrderAddress']?.['useAddressInformation'];
      this.useDeliveryAddressAsInvoiceAddress = data?.['OrderAddress']?.['useDeliveryAddressAsInvoiceAddress']
      this.userId = data?.['OrderAddress']?.['userId']
      this.paymentMethod = data?.['paymentMethod']
      this.orderItem = data?.['orderItem'];
    })
  }

  checkIfAllFeildExist() {
    return (this.deliveryAddress !== undefined && this.invoiceAddress !== undefined && this.useAddressInformation !== undefined && this.useDeliveryAddressAsInvoiceAddress !== undefined && this.paymentMethod !== undefined && this.orderItem.length > 0)
  }

  checkIfAllDateExist() {

    this.checkNotEmptyField = Object.keys(this.ProductCartService.orderOverViewData$value).every((value) => (this.ProductCartService.orderOverViewData$value[value] !== "" || this.ProductCartService.orderOverViewData$value[value] !== 0));

    this.checkIfProductExist = this.ProductCartService.orderOverViewData$value['orderItem'].length > 0

    return (this.checkNotEmptyField && this.checkIfProductExist);
  }
  payPalItems: Array<OrderPaypal> = []
  prepareOrdersToPayPal() {
    this.ProductCartService.orderOverViewData$value['orderItem'].map(order => {
      this.payPalItems.push(new OrderPaypal(
        order.productName,
        order.quantity,
        order.categoryName + " " + order.productName,
        order.userId + " " + order.categoryName + " " + order.productName,
        new UnitAmount(order.productPrice)
      ))
    })
  }

  @ViewChild('paypal', { static: true }) paypalElement: ElementRef;
  paidFor = false;
  paypalButton = 0
  payPalFun() {
    this.prepareOrdersToPayPal()
    paypal
      .Buttons({
        style: {
          color: 'gold',
          height: 40,
          label: 'paypal',
          layout: 'vertical',
          // layout: 'horizontal',
          shape: 'pill',
          // tagline: true
        },
        createOrder: (data, actions) => {
          return actions.order.create({
            purchase_units: [
              {
                amount: {
                  value: `${this.ProductCartService.productsTotalPrice}`,
                  breakdown: {
                    item_total: {
                      value: `${this.ProductCartService.productsTotalPrice}`,
                      currency_code: 'USD',
                    },
                    discount: {
                      value: '0',
                      currency_code: 'USD',
                    },
                    handling: {
                      value: '0',
                      currency_code: 'USD',
                    },
                    insurance: {
                      value: '0',
                      currency_code: 'USD',
                    },
                    shipping: {
                      value: '0',
                      currency_code: 'USD',
                    },
                    shipping_discount: {
                      value: '0',
                      currency_code: 'USD',
                    },
                    tax_total: {
                      value: '0',
                      currency_code: 'USD',
                    }
                  }
                },
                description: "order from vybes",
                items: this.payPalItems
              }
            ]
          },
          )
        },
        onInit: (data, actions) => {
        },
        onApprove: async (data, actions) => {
          const order = await actions.order.capture();
          this.comfirmOrderWithBack()
          this.paidFor = true
        },
        onError: err => {
          console.log(err);
          // this.paidFor = true
          return this.alertService.error('please contact with manger to solve this issue')
        },
        onCancel: (data) => {
          console.log('onCancel')
          // this.paidFor = true
        }
      })
      .render(this.paypalElement.nativeElement);
  }

  confirmOrder() {
    if (!this.checkIfAllDateExist() || !this.checkIfAllFeildExist()) {
      return this.alertService.error('please fill missing data', true)
    }
    if (this.paymentMethod === 'paypal') {
      this.payPalFun()
    }
  }

  comfirmOrderWithBack() {
    let order = this.ProductCartService.orderOverViewData$value['OrderAddress']
    this.ProductCartService.addOrder(order).subscribe(data => {
      return this.router.navigate(['products/thank-you', { orderId: data['data'] }])
    }, (err) => {
      return this.alertService.error('please contact with manager to solve this issue')
    })
  }

  back() {
    this.changeTap.emit('previous');
  }

}
